.visually-hidden:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

body, html {
    min-width: 320px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 12px;
    background-color: $white;
}

img {
    max-width: 100%;
    height: auto;
}

.container {
    width: 924px;
    margin: 0 auto; 
    padding-left: 15px;
    padding-right: 15px;   

    @media (max-width: 955px) {
        width: 624px;
    }

    @media (max-width: 683px) {
        width: 100%;
        margin: 0;
        padding-left: 0;
        padding-right: 0;
    }

}

.full-width {
    padding-left: 24px;
    padding-right: 24px;
}






